<script>
import ironStore from "@/ironStore.js";
export default {
  computed: {
    day: {
      get() {
        return this.$store.state.menuList ? this.$store.state.menuList.day : 0;
      },
      set(val) {
        //不能直接改store
        // this.$store.state.day = val;

        //自行變更day值
        this.$store.commit("menuList/SETDAY", val);
      },
    },
    header() {
      return this.$store.state.menuList
        ? this.$store.state.menuList.header
        : null;
    },
    currentDay() {
      return this.$store.state.menuList
        ? this.$store.getters["menuList/currentDay"]
        : null;
    },
    // list() {
    //   return this.$store.state.menuList ? this.$store.state.menuList.list : [];
    // },
  },
  methods: {
    changeIndex(change) {
      let day = (this.day + change + 30) % 30;
      this.$router.replace({
        params: {
          day: day + 1,
        },
      });
    },
  },
  mounted() {
    let day = parseInt(this.$route.params.day) - 1;
    // this.day = day;
    this.$store.registerModule("menuList", ironStore);
    this.$store.dispatch("menuList/GETLIST", day);
  },
  beforeDestroy() {
    this.$store.unregisterModule("menuList");
  },
  watch: {
    $route() {
      let day = parseInt(this.$route.params.day) - 1;
      this.day = day;
    },
  },
};
</script>

<template src="./template.html"></template>
<style src="./style.css" scoped></style>
