// import axios from "axios";
export default {
  namespaced: true,
  state: {
    day: 0,
    header: {
      title: "Vue 框架應用",
    },
    list: [],
  },
  getters: {
    currentDay(state) {
      return state.list[state.day];
    },
  },
  mutations: {
    SETDAY(state, day) {
      state.day = day;
    },
    SETLIST(state, list) {
      state.list = list;
    },
  },
  actions: {
    GETLIST(context, day) {
      context.commit("SETDAY", day);

      var requestURL = "https://chen-yuzhen.github.io/vue-practice/list.json";
      var request = new XMLHttpRequest();
      request.open("GET", requestURL);
      request.responseType = "json";
      request.send();

      request.onload = function() {
        var res = request.response;
        context.commit("SETLIST", res);
      };

      // return axios
      //   .get(
      //     "https://chen-yuzhen.github.io/vue-practice/list.json"
      //   )
      //   .then((res) => {
      //     context.commit("SETLIST", res.data);
      //   });
    },
  },
};
